import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

var newestarticles = () => import ('../components/newestarticles')
var bod = () => import ('../components/body')
var a1 = () => import ('../articles/article1')
var a2 = () => import ('../articles/a2')

var router =  new Router({
    routes: [{
        path: '/',
        component: bod
    }, {
        path: '/newest',
        component: newestarticles
    }, {
        path: '/a1',
        component: a1
    }, {
        path: '/a2',
        component: a2
    }]
})
var routespaths = ['/','/newest'];
var savescrolltop = 0;
router.afterEach(function(to, from, next){
    console.log(to.path,from.path)
    var stopForEach = false;
    routespaths.forEach(function(item){
        if(stopForEach) return;
        if(from.path == item){
            savescrolltop = window.scrollY;
            goScrollTop(0,false)
            stopForEach = true;
        }else{
            goScrollTop(savescrolltop,true)
            stopForEach = true;
        }
    })
})
function goScrollTop(Y,K){
    if(K){
        // 写延时是为了防止浏览器返回自动置顶
        setTimeout(function(){
            window.scrollTo(0,Y);
        },50)
    }else{
        window.scrollTo(0,Y);
    }
}
export default router


