<template>
    <div id="app" ref="App">
        <div class="ground_bg"></div>
        <Header/>
        <div class="bodywrap">
            <transition name="fadeFromBottom">
                <router-view></router-view>
            </transition>
        </div>
        <Foote/>
    </div>
</template>

<script>
    import Header from './components/heade'
    import Foote from './components/foote'

    export default {
        name: 'App',
        data(){
            return{
                transitionName: '',
                appHeight: null,
            }
        },
        components: {
            Header,
            Foote
        }
    };
</script>

<style>
    *{ padding:0px; margin:0px }
    body, div, dl, dt, dd, ul, ol, li,h1, h2, h3, h4, h5, h6, pre, code,form,fieldset, 
    legend, input, button,textarea, p, blockquote, th, td { margin: 0; padding: 0;}
    body{ color:#000; margin: 0px auto; font-family:"Microsoft YaHei"; font-size:14px; overflow-x: hidden; }
    p{ line-height:1.8em }
    li{ list-style: none outside none; }
    a{ display: block; text-decoration:none; outline:none; color: #444; }

    /*-------------------初始化结束------------------*/
    #app{ position: relative; overflow-x: hidden; }
    .ground_bg{ background: url(https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1555933553406&di=0301c8c36cd81a0b40a71426334a4e5a&imgtype=0&src=http%3A%2F%2Fi0.hdslb.com%2Fbfs%2Farticle%2Fd2c216c4b538b175fdd3a24bd1a9c479decfd1f3.jpg) top center no-repeat;
        -moz-background-size: cover; -o-background-size: cover; -webkit-background-size: cover; background-size: cover;
        top: 0; left: 0; position: fixed; width: 100%; height: 100%; z-index: -999; }
    .bodywrap{ background-color: #FFF; }
    .infoarticlewrap{ background: #FFF; padding: 15px; }
    .infoarticletitle{ font-size: 20px; color: #444; text-align: center; }
    .statisticswrap{ padding: 10px 0; font-size: 12px; color: #999; display: flex; justify-content: center; }
    .statisticswrap>span{ padding: 0 4px; }
    .infoarticlecontent{ font-size: 15px; color: #666; line-height: 2.0em; max-width: 800px; margin: 0 auto; }
    .infoarticlecontent img{ display: block; max-width: 100%; margin: 0 auto; padding: 20px 0; }
    .router-link-exact-active{ color: #ccc !important; }

    @media (max-width: 800px){
        .router-link-exact-active{ background: #444; }
    }

    .fadeFromBottom-enter-active{
        -webkit-animation: fadeFromBottom 1s both;
        animation: fadeFromBottom 1s both;
    }
    /*-------------------公用CSS3动画------------------*/

    @charset "UTF-8";
    /*--s--*/
    @-webkit-keyframes slideFadeOutRight {
        from {
            opacity: 0;
            -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
        }
        to {
            opacity: 1;
            -webkit-transform: translateX(0);
            transform: translateX(0);
        }
    }
    @keyframes slideFadeOutRight {
        from {
            opacity: 0;
            -webkit-transform: translateX(100%);
            transform: translateX(100%);
        }
        to {
            opacity: 1;
            -webkit-transform: translateX(0);
            transform: translateX(0);
        }
    }
    .slideFadeOutRight{
        -webkit-animation: slideFadeOutRight 1s both;
        animation: slideFadeOutRight 1s both;
    }
    /*--f--*/

    /*--s--*/
    @-webkit-keyframes slideFadeToRight {
        from {
            opacity: 1;
            -webkit-transform: translateX(0);
            transform: translateX(0);
        }
        to {
            opacity: 0;
            -webkit-transform: translateX(100%);
            transform: translateX(100%);
        }
    }
    @keyframes slideFadeToRight {
        from {
            opacity: 1;
            -webkit-transform: translateX(0);
            transform: translateX(0);
        }
        to {
            opacity: 0;
            -webkit-transform: translateX(100%);
            transform: translateX(100%);
        }
    }
    .slideFadeToRight{
        -webkit-animation: slideFadeToRight 1s both;
        animation: slideFadeToRight 1s both;
    }
    /*--f--*/

    /*--s--*/
    @-webkit-keyframes clockwiseRotate {
        from {
            -webkit-transform: translateY(0) rotate(0deg);
            transform: translateY(0) rotate(0deg);
        }
        to {
            -webkit-transform: translateY(11.5px) rotate(45deg);
            transform: translateY(11.5px) rotate(45deg);
        }
    }
    @keyframes clockwiseRotate {
        from {
            -webkit-transform: translateY(0) rotate(0deg);
            transform: translateY(0) rotate(0deg);
        }
        to {
            -webkit-transform: translateY(11.5px) rotate(45deg);
            transform: translateY(11.5px) rotate(45deg);
        }
    }
    @-webkit-keyframes anticlockwiseRotate {
        from {
            -webkit-transform: translateY(0) rotate(0deg);
            transform: translateY(0) rotate(0deg);
        }
        to {
            -webkit-transform: translateY(-11.5px) rotate(-45deg);
            transform: translateY(-11.5px) rotate(-45deg);
        }
    }
    @keyframes anticlockwiseRotate {
        from {
            -webkit-transform: translateY(0) rotate(0deg);
            transform: translateY(0) rotate(0deg);
        }
        to {
            -webkit-transform: translateY(-11.5px) rotate(-45deg);
            transform: translateY(-11.5px) rotate(-45deg);
        }
    }
    /*r*/
    @-webkit-keyframes clockwiseRotatereverse {
        from {
            -webkit-transform: translateY(11.5px) rotate(45deg);
            transform: translateY(11.5px) rotate(45deg);
        }
        to {
            -webkit-transform: translateY(0) rotate(0deg);
            transform: translateY(0) rotate(0deg);
        }
    }
    @keyframes clockwiseRotatereverse {
        from {
            -webkit-transform: translateY(11.5px) rotate(45deg);
            transform: translateY(11.5px) rotate(45deg);
        }
        to {
            -webkit-transform: translateY(0) rotate(0deg);
            transform: translateY(0) rotate(0deg);
        }
    }
    @-webkit-keyframes anticlockwiseRotatereverse {
        from {
            -webkit-transform: translateY(-11.5px) rotate(-45deg);
            transform: translateY(-11.5px) rotate(-45deg);
        }
        to {
            -webkit-transform: translateY(0) rotate(0deg);
            transform: translateY(0) rotate(0deg);
        }
    }
    @keyframes anticlockwiseRotatereverse {
        from {
            -webkit-transform: translateY(-11.5px) rotate(-45deg);
            transform: translateY(-11.5px) rotate(-45deg);
        }
        to {
            -webkit-transform: translateY(0) rotate(0deg);
            transform: translateY(0) rotate(0deg);
        }
    }
    .clockwiseRotate{
        animation: clockwiseRotate 1s both;
        -webkit-animation: clockwiseRotate 1s both;
    }
    .clockwiseRotatereverse{
        animation: clockwiseRotatereverse 1s both;
        -webkit-animation: clockwiseRotatereverse 1s both;
    }
    .anticlockwiseRotate{
        animation: anticlockwiseRotate 1s both;
        -webkit-animation: anticlockwiseRotate 1s both;
    }
    .anticlockwiseRotatereverse{
        animation: anticlockwiseRotatereverse 1s both;
        -webkit-animation: anticlockwiseRotatereverse 1s both;
    }
    /*--f--*/

    /*--s--*/
    @-webkit-keyframes fadeFromBottom {
        from {
            opacity: 0;
            -webkit-transform: translateY(50px);
            transform: translateY(50px);
        }
        to {
            opacity: 1;
            -webkit-transform: translateY(0);
            transform: translateY(0);
        }
    }
    @keyframes fadeFromBottom {
        from {
            opacity: 0;
            -webkit-transform: translateY(50px);
            transform: translateY(50px);
        }
        to {
            opacity: 1;
            -webkit-transform: translateY(0);
            transform: translateY(0);
        }
    }
    .fadeFromBottom{
        -webkit-animation: fadeFromBottom 1s both;
        animation: fadeFromBottom 1s both;
    }
    /*--f--*/

    /*--s--*/
    @-webkit-keyframes scaleOut {
        from {
            opacity: 1;
            -webkit-transform: scale(1);
            transform: scale(1);
        }
        to {
            opacity: 0;
            -webkit-transform: scale(0);
            transform: scale(0);
        }
    }
    @keyframes scaleOut {
        from {
            opacity: 1;
            -webkit-transform: scale(1);
            transform: scale(1);
        }
        to {
            opacity: 0;
            -webkit-transform: scale(0);
            transform: scale(0);
        }
    }
    .scaleOut{
        -webkit-animation: scaleOut 1s both;
        animation: scaleOut 1s both;
    }
    @-webkit-keyframes scaleIn {
        from {
            opacity: 0;
            -webkit-transform: scale(0);
            transform: scale(0);
        }
        to {
            opacity: 1;
            -webkit-transform: scale(1);
            transform: scale(1);
        }
    }
    @keyframes scaleIn {
        from {
            opacity: 0;
            -webkit-transform: scale(0);
            transform: scale(0);
        }
        to {
            opacity: 1;
            -webkit-transform: scale(1);
            transform: scale(1);
        }
    }
    .scaleIn{
        -webkit-animation: scaleIn 1s both;
        animation: scaleIn 1s both;
    }
    /*--f--*/
</style>



