<template>
	<footer>
		<transition name="showFoote">
			<div class="footerWrap" v-show="showFooter">
				<div class="contactwrap">
					<i class="WeChat"></i>
					<i class="QQ"></i>
				</div>
		        <div class="flowWrap" id="busuanzi_container_site_pv">本站总访问量<span id="busuanzi_value_site_pv"></span>次</div>
	        </div>
		</transition>
	</footer>
</template>
<script>
	export default{
		name: 'foot',
		data () {
            return {
            	appHeight: 0,
            	showFooter: false
            }
        },
        mounted (){
            window.addEventListener("scroll",this.roll);
        },
        methods: {
            roll: function(){
            	console.log(1)
                if(!this.appHeight){
                    this.appHeight = document.querySelector("#app").offsetHeight;
                }
                if( window.scrollY - 0 + window.innerHeight >=  this.appHeight - 100 ){
                    this.showFooter = true;
                }else{
                	this.showFooter = false;
                }
            }
        }
	};
</script>
<style scoped>
	footer{ position: relative; width: 100%; height: 400px; background: rgba(0,0,0,.4); display: flex; flex-direction: column; align-items: center; justify-content: center; overflow: hidden; }
	footer:after{ content: ""; display: block; width: 200%; height: 100%; border-radius: 50%; background: #FFF;
		position: absolute; left: 0; right: 0; top: -350px; margin-left: -50%;
	}
	.footerWrap{ display: flex; flex-direction: column; align-items: center; }
	.contactwrap{ display: flex; justify-content: center; }
	.contactwrap i{ display: block; width: 80px; height: 80px; margin: 0 20px; background: #FFF; }
	.contactwrap i.WeChat{ background: url(../assets/WeChat.png); background-size: 100%; }
	.contactwrap i.QQ{ background: url(../assets/QQ.png); background-size: 100%; }
	#busuanzi_container_site_pv{ color: #FFF; margin-top: 20px; }

	.showFoote-enter-active{
        -webkit-animation: scaleIn 1s;
        animation: scaleIn 1s;
	}
	.showFoote-leave-active {
        -webkit-animation: scaleOut 1s;
        animation: scaleOut 1s;
	}
	/*---媒体查询---*/
	@media (max-width: 800px){
		footer{ height: 300px; }
		footer:after{ top: -270px; }
	}
</style>


